@use "vars" as *;


.inner {
	display: block;
	max-width: 1240px;
	padding: 0 20px;
	margin: 0 auto;
}

.heading-note{
	line-height: 1.875;
	letter-spacing: 1px;
	margin-bottom: 60px;
}


.fex-center{
	@include flex(center,center,wrap);
	.c-btn__02{
		margin: 0 15px;
		max-width: 230px;
	}
}

@keyframes hero_anime {
	0% {
		-webkit-mask-position: -100% 0;
	}
	100% {
		-webkit-mask-position: 100% 0;
	}
}
