@use "vars" as *;

#header{
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: 999;
	background: rgba(white,0.9);
	padding: 15px 40px;
	.header-logo{
		display: block;
		width: 220px;
		position: absolute;
		@include v-align;
		left: 40px;
	}
	.header-nav{
		display: flex;
		justify-content: flex-end;
		flex-wrap: wrap;
		align-content: center;
		@include mq(tb){
			display: none;
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			height: 100vh;
			padding: 60px 20px;
			overflow-y: scroll;
			&.active{
				display: block;
			}
		}
		&__tel{
			display: flex;
			justify-content: flex-end;
			align-items: flex-end;
			line-height: 1;
			width: 100%;
			@include font-size(12);
			margin-bottom: 11px;
			p{
				padding: 0 0 4px 12px;
			}
			a{
				display: block;
				@include font-size(22);
				color: $blue;
				font-weight: bold;
				padding: 0 0 0 40px;
				position: relative;
				&:before{
					content: "";
					width: 28px;
					height: 28px;
					border-radius: 50%;
					background: $gray;
					position: absolute;
					left: 0;
					@include v-align;
				}
				&:after{
					content: "";
					width: 13px;
					height: 13px;
					background: url("../img/common/icon-tel.png")no-repeat;
					background-size: 100%;
					position: absolute;
					left: 8px;
					@include v-align;
				}
			}
		}
		&__menu{
			display: flex;
			flex-wrap: wrap;
			justify-content: flex-end;
			align-items: center;
			width: calc(100% - 255px);
			padding-right: 15px;
			&-item{
				padding: 0 10px;
				a{
					display: block;
					@include font-size(15);
					font-weight: 500;
					color: #161616;
					padding: 0 0 5px;
					position: relative;
					&:after{
						content: "";
						position: absolute;
						bottom: 0;
						left: 0;
						right: 0;
						height: 1px;
						background: $blue;
						@include smooth-transition;
						opacity: 0;
						visibility: hidden;
					}
					@include mq(tb-min){
						&.active,&:hover{
							opacity: 1;
							color: $blue;
							&:after{
								opacity: 1;
								visibility: visible;
							}
						}
					}
				}
			}
		}
		&__btn{
			width: 255px;
			@include flex(between,center,wrap);
			.c-btn__01{
				max-width: 123px;
				height: 40px;
			}
		}
	}
	.js-mobile {
		width: 26px;
		height: 30px;
		position: absolute;
		right: 20px;
		@include v-align;
		cursor: pointer;
		z-index: 12;
		display: none;
		@include mq(tb){
			display: block;
		}
		span{
			position: absolute;
			@include v-align;
			display: block;
			width: 100%;
			height: 2px;
			background: #333;
			border-radius: 3px;
			&:before,&:after{
				position: absolute;
				left: 0;
				width: 100%;
				height: 100%;
				background: #333;
				content: '';
				-webkit-transition: -webkit-transform 0.3s;
				transition: transform 0.3s;
				border-radius: 3px;
			}
			&:before{
				-webkit-transform: translateY(-250%);
				transform: translateY(-250%);
				top: -6px;
			}
			&:after{
				-webkit-transform: translateY(250%);
				transform: translateY(250%);
				bottom: -6px;
			}
		}
		&--close {
			span{
				background-color: transparent;
				&:before,&:after{
					width: 100%;
					background: #333;
				}
				&:before {
					-webkit-transform: translateY(0) rotate(45deg);
					transform: translateY(0) rotate(45deg);
					top: 2px;
				}
				&:after {
					-webkit-transform: translateY(0) rotate(-45deg);
					transform: translateY(0) rotate(-45deg);
					bottom: -2px;
				}
			}
		}
	}
	&.top-page{
		position: absolute;
		background: none;
		.header-logo{
			opacity: 0;
			visibility: hidden;
		}
		.header-nav__tel{
			outline: 0;
			visibility: hidden;
		}
		.header-nav__btn{
			display: none;
		}
	}
}


.js-locked {
	overflow: hidden;
	-ms-touch-action: none;
	touch-action: none;
	body {
		-webkit-overflow-scrolling: auto;
	}
}