@use "vars" as *;

.mv-slider{
	.slick-arrow{
		display: none !important;
	}
	&__item{
		position: relative;
		width: 100%;
		height: 100vh;
		min-height: 750px;
		overflow: hidden;

		&.slick-current {
			-webkit-mask-image: radial-gradient(circle farthest-corner at 100% 0%, #000 60%, transparent 90%);
			-webkit-mask-repeat: no-repeat;
			-webkit-mask-size: 200% 100%;
			-webkit-mask-position: -100% 0;
			transition-property: -webkit-mask-position;
			transition-property: mask-position;
			transition-property: mask-position, -webkit-mask-position;
			transition-duration: 5s;
			transition-timing-function: cubic-bezier(0.3, 0.1, 0.1, 1);
			animation: hero_anime 2s forwards;
			.mv-slider__img{
				transform: scale(1.04);
			}
		}
		&.preve-slide{
			opacity: 1 !important;
		}
	}
	&__img{
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 0;
		background-position: center;
		background-size: cover;
		transition: 10s ease-in-out;
		&:after{
			content: "";
			position: absolute;
			bottom: 0;
			left: 0;
			right: 0;
			height: 80%;
			background: url("../img/index/bg-mv.png")no-repeat;
			background-size: 100%;
			background-position: bottom center;
		}
		&--01{
			background: url("../img/index/mv-01.jpg")no-repeat;
			background-size: cover;
			background-position: center;
		}
		&--02{
			background: url("../img/index/mv-02.jpg")no-repeat;
			background-size: cover;
			background-position: center;
		}
		&--03{
			background: url("../img/index/mv-03.jpg")no-repeat;
			background-size: cover;
			background-position: center;
		}
		&--04{
			background: url("../img/index/mv-04.jpg")no-repeat;
			background-size: cover;
			background-position: center;
		}
		&--05{
			background: url("../img/index/mv-05.jpg")no-repeat;
			background-size: cover;
			background-position: center;
		}
	}
}
.mv-heading{
	position: absolute;
	bottom: 68px;
	right: 50px;
	max-width: 475px;
	@include fts(16.28);
	color: white;
	letter-spacing: 1px;
	&__ttl{
		display: block;
		@include fts(30,45);
		font-weight: bold;
		margin-bottom: 20px;
	}
}
.mv-sidebar{
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	width: 282px;
	background: rgba(white,0.9);
	text-align: center;
	padding: 60px 15px 40px;
	display: flex;
	flex-wrap: wrap;
	align-content: space-between;
	&__item{
		width: 100%;
	}
	&__note{
		@include font-size(14);
		color: #23375F;
		font-weight: 500;
		span{
			display: inline-block;
			position: relative;
			padding: 0 25px;
			margin-bottom: 30px;
			&:before,&:after{
				position: absolute;
				bottom: 0;
			}
			&:before{
				content: "＼";
				left: 0;
			}
			&:after{
				content: "／";
				right: 0;
			}
		}
	}
	&__logo{
		display: block;
		width: 160px;
		margin: 0 auto 30px;
	}
	&__ct{
		img{
			display: block;
			width: 46px;
			margin: 0 auto;
		}
	}
	&__tel{
		position: relative;
		@include font-size(13);
		padding: 35px 0 0;
		margin-bottom: 40px;
		&:before{
			content: "";
			width: 30px;
			height: 30px;
			border-radius: 50%;
			background: $gray;
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			margin: 0 auto;
		}
		&:after{
			content: "";
			width: 15px;
			height: 15px;
			background: url("../img/common/icon-tel.png")no-repeat;
			background-size: 100%;
			position: absolute;
			top: 8px;
			left: 0;
			right: 0;
			margin: 0 auto;
		}
		span{
			display: block;
			@include font-size(24);
			font-weight: bold;
			color: $blue;
		}
	}
	&__btn{
		max-width: 210px;
		margin: 0 auto;
		.c-btn__01{
			margin-bottom: 20px;
			&:last-child{
				margin-bottom: 0;
			}
		}
	}
}

.sec-about{
	padding: 155px 0 250px;
	background: #F8F9F9;
}
.about-block{
	@include flex(0,0,wrap);
	&__img{
		width: 51.5%;
	}
	&__ct{
		width: 48.5%;
		padding: 17.5% 0 0 50px;
		letter-spacing: 1px;
		&-ttl{
			display: block;
			margin-bottom: 20px;
			span{
				display: block;
			}
			.c-ft--en{
				color: $pink;
				@include font-size(40);
				line-height: 1.4;
				margin-bottom: 7px;
			}
			.c-ft--jp{
				@include fts(26,44);
				font-weight: bold;
			}
		}
	}
}


.sec-features{
	background: white;
	border-radius: 100px 100px 0 0;
	margin: -100px 0 0;
	padding: 153px 0;
}
.features-list{
	@include flex(center,0,wrap);
	margin: 0 -20px 25px;
	&__item{
		width: 33.333%;
		padding: 0 20px;
		margin-bottom: 40px;
	}
	&__box{
		border: 1px solid #E6ECEE;
		height: 100%;
	}
	&__img{
		img{
			width: 100%;
		}
	}
	&__ct{
		@include fts(15,25);
		letter-spacing: 1px;
		padding: 20px 27px 29px;
		&-count{
			display: block;
			text-align: center;
			font-weight: bold;
			color: $blue;
			margin-bottom: 5px;
		}
		&-ttl{
			@include flex(center,center,wrap);
			@include fts(18,26);
			text-align: center;
			font-weight: bold;
			min-height: 52px;
			margin-bottom: 13px;
		}
	}
}


.sec-service{
	background: #F8F9F9;
	padding: 152px 0;
}

.service-list{
	@include flex(center,0,wrap);
	margin: 0 -15px 30px;
	&__item{
		width: 20%;
		padding: 0 15px;
		margin-bottom: 30px;
	}
	&__box{
		background: white;
		border-radius: 20px;
		box-shadow: 0 0 15px 0 rgba(#27292B,0.08);
		height: 100%;
		padding: 41px 30px 20px;
		min-height: 233px;
	}
	&__img{
		width: 90px;
		height: 90px;
		margin: 0 auto 20px;
	}
	&__ct{
		text-align: center;
		&-ttl{
			display: block;
			span{
				display: block;
			}
			.c-ft--jp{
				@include font-size(16);
				font-weight: bold;
				color: $blue;
				margin-bottom: 4px;
			}
			.c-ft--en{
				@include font-size(10);
				font-family: $font-jp;
				font-weight: 500;
				color: $pink;
			}
		}
	}
}